import AppConstants from '../constants/AppConstants';
import Dispatcher from '../dispatcher/AppDispatcher';
import BaseStore from './BaseStore';


let user = null,
  users = null,
  session = null,
  signInResult = null,
  signOutResult = null,
  signUpResult = null,
  updateResult = null;

class UserStore extends BaseStore {
  constructor() {
    super();
  }
  getUsers() {
    return users;
  }

  setUsers(result) {
    users = result;
    this.emitChange();
  }

  getUser() {
    return user;
  }

  setUser(result) {
    user = result;
    this.emitChange('userInfo');
  }

  setSession(result) {
    session = result;
    this.emitChange('session');
  }

  getSession() {
    return session;
  }

  setSignInResult(signIn) {
    signInResult = signIn;
    this.emitChange('signIn');
  }

  getSignInResult() {
    return signInResult;
  }

  setSignOutResult(signOut) {
    signOutResult = signOut;
    this.emitChange('signOut');
  }

  getSignOutResult() {
    return signOutResult;
  }

  setSignUpResult(signUp) {
    signUpResult = signUp;
    this.emitChange('signUp');
  }

  getSignUpResult() {
    return signUpResult;
  }

  setUpdateResult(update) {
    updateResult = update;
    this.emitChange('userUpdate');
  }

  getUpdateResult() {
    return updateResult;
  }
}

let userStore = new UserStore();

userStore.dispatchToken = Dispatcher.register(action => {
  switch (action.actionType) {
    case AppConstants.USER_SIGNIN:
      userStore.setSignInResult(action.data);
      break;
    case AppConstants.USER_SIGNOUT:
      userStore.setSignOutResult(action.data);
      break;
    case AppConstants.USER_SIGNUP:
      userStore.setSignUpResult(action.data);
      break;
    case AppConstants.USER_SESSION:
      userStore.setSession(action.data);
      break;
    case AppConstants.UPDATE_USER:
      userStore.setUpdateResult(action.data);
      break;
    case AppConstants.GET_USERS:
      userStore.setUsers(action.data);
      break;
    case AppConstants.GET_USER:
      userStore.setUser(action.data);
      break;
    default:
      return;
  }

  userStore.emitChange();

});


export default userStore;



